import {mapState,mapMutations} from 'vuex'

export default {
	computed:{
		...mapState(['currPlayList','currSongIndex'])
	},
	methods:{
		...mapMutations([
			'nextSongIndex',
			'prevSongIndex',
			'randomSongIndex',
			'changeCurrSong'
		]),
		//
		nextSong(){
			this.nextSongIndex()
			this.changeCurrSong(this.currPlayList[this.currSongIndex])
		},
		//
		prevSong(){
			this.prevSongIndex()
			this.changeCurrSong(this.currPlayList[this.currSongIndex])
		},
		//
		loopPlay(){
			this.nextSong()
		},
		//
		randomPlay(){
			this.randomSongIndex()
			this.changeCurrSong(this.currPlayList[this.currSongIndex])
		},
		//
		singlePlay(){
			this.audioContext.play()
			// this.paused = false
		}
	}
}


